import React, { useState } from "react";
import "./OtherOrdersItem.scss";

const OtherOrdersItem = ({ order }) => {
  const [showBody, setShowBody] = useState(false);
  const dateToFormat = order.date_created;
  const dateArray = dateToFormat.split("T")[0].split("-");
  const date = `${dateArray[2]}/${dateArray[1]}/${dateArray[0]}`;

  const name = order.payer.name;
  const email = order.payer.redes;

  const total = order.items.reduce((prev, item) => {
    console.log("price", item);
    return prev + item.price;
  }, 0);

  let type = order.type.toUpperCase();
  if (order.type === "en-mano") type = "EN MANO";
  return (
    <div className='other-order-item'>
      <div className='other-order-item__header' onClick={() => setShowBody(!showBody)}>
        <h3>{date}</h3>
        <h3>{name}</h3>
        <h3>${total}</h3>
        <h3>{type}</h3>
      </div>

      <div className={`other-order-item__body ${showBody ? "show" : "hidden"}`}>
        <article className='other-order-item__body-container'>
          <section className='other-order-item__body--right'>
            <h2>INFO</h2>
            <h3>
              Email/Redes: <span>{email}</span>
            </h3>
            <h3>
              Nombre: <span>{name}</span>
            </h3>

            <h2 style={{ marginTop: "10px" }}>ENVIO</h2>

            <div className='shipping-container'>
              <h3>
                Entrega: <span>{order.custom_shipment.delivery_date}</span>
              </h3>
              <h3>
                Observaciones: <span>{order.custom_shipment.observation}</span>
              </h3>
            </div>
          </section>
          <section className='other-order-item__body--left'>
            <h2>COMPRA:</h2>
            {order.items &&
              order.items.map((item) => {
                console.log(item);
                return (
                  <div className='order-container'>
                    <h3>{item ? item.name : ""}</h3>
                    <h3>TALLE: {item ? item.size.toUpperCase() : ""}</h3>
                    <h3>CANTIDAD: {item ? item.quantity : ""}</h3>
                  </div>
                );
              })}
          </section>
        </article>
      </div>
    </div>
  );
};

export default OtherOrdersItem;
