import React, { useEffect } from "react";
import { connect } from "react-redux";
import { getOtherOrders, setShowNewOtherOrders } from "../../store/actions/generalActions";
import Button from "../../components/Button/Button";
import NewOtherOrder from "../../components/NewOtherOrder/NewOtherOrder";
import OtherOrdersItem from "../../components/OtherOrdersItem/OtherOrdersItem";
import "./OtherOrders.scss";
const OtherOrders = ({ getOtherOrders, otherOrders, showNewOtherOrders, setShowNewOtherOrders }) => {
  useEffect(() => {
    getOtherOrders();
  }, []);

  console.log(otherOrders);

  return (
    <div className='other-orders'>
      <div className='view-title other-orders_title'>
        <h1>OTRAS ORDENES</h1>
        <Button onClick={setShowNewOtherOrders}>NUEVA ORDEN</Button>
      </div>
      {showNewOtherOrders ? <NewOtherOrder /> : null}
      <div className='other-orders-container'>
        {otherOrders &&
          otherOrders.map((otherOrder) => {
            return <OtherOrdersItem order={otherOrder} />;
          })}
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    otherOrders: state.otherOrders,
    showNewOtherOrders: state.showNewOtherOrders,
  };
};

export default connect(mapStateToProps, { getOtherOrders, setShowNewOtherOrders })(OtherOrders);
