import React, { useState } from "react";
import "./NewOtherOrder.scss";
import { connect } from "react-redux";
import StockItem from "../Stock/StockItem/StockItem";
import Button from "../Button/Button";
import { createOtherOrder, setShowNewOtherOrders } from "../../store/actions/generalActions";
import { ReactComponent as Close } from "../../assets/close.svg";
const NewOtherOrder = ({ stock, createOtherOrder, setShowNewOtherOrders }) => {
  // onChange={(e) => infoUpdateHandler(e.target.value, "name")}

  const [typeOrder, setTypeOrder] = useState("");

  const [discount, setDiscount] = useState(false);

  const [stockFromSelected, setStockFromSelected] = useState(null);
  const [selected, setSelected] = useState(null);
  const [selectedSize, setSelectedSize] = useState(null);
  const [toAddValue, setToAddValue] = useState(0);

  const [articlesToSend, setArticlesToSend] = useState([]);

  const [info, setInfo] = useState({
    name: "",
    redes: "",
    observation: "",
    delivery_date: "",
  });

  const infoHandler = (value, type) => {
    const newInfo = { ...info };
    newInfo[type] = value;
    setInfo(newInfo);
  };

  const resetValues = () => {
    setStockFromSelected(null);
    setSelected(null);
    setToAddValue(null);
    setSelectedSize(0);
  };

  const selectItemHandler = (item) => {
    let itemFormated = JSON.parse(item);
    setSelected(itemFormated);
    setStockFromSelected(itemFormated.stock);
  };

  const selectedSizeHandler = (size) => {
    let sizeFormatted = JSON.parse(size);
    setSelectedSize(sizeFormatted);
  };

  const addArticleToSend = () => {
    const toSendArticle = {
      category_id: selected._id,
      id: selectedSize._id,
      size: selectedSize.size,
      name: selected.name,
      quantity: toAddValue,
      price: selected.price,
    };
    setArticlesToSend(articlesToSend.concat(toSendArticle));
    resetValues();
  };

  const deleteArticleToSend = (index) => {
    console.log(index);
    const newArticlesToSend = articlesToSend.filter((article, i) => {
      console.log(i);
      return i !== index;
    });

    console.log(newArticlesToSend);

    setArticlesToSend(newArticlesToSend);
  };

  const validationHandler = () => {
    return articlesToSend.length > 0 && typeOrder && info.name && info.redes && info.observation && info.delivery_date;
  };

  let isValid = validationHandler();

  const createNewOrder = () => {
    const newOrder = {
      items: articlesToSend,
      payer: {
        name: info.name,
        redes: info.redes,
      },
      type: typeOrder,
      discount: discount,
      custom_shipment: {
        observation: info.observation,
        delivery_date: info.delivery_date,
      },
    };
    createOtherOrder(newOrder);
  };

  return (
    <div className='new-other-order'>
      <h2 className='new-other-order_title'>Nueva Orden</h2>
      <Close className='close-order' onClick={setShowNewOtherOrders} />
      <article className='new-other-order-container'>
        <section className='new-other-order--left'>
          <h2 className='new-other-order_header'>Persona:</h2>
          <div className='new-other-order-item'>
            <h3>Nombre</h3>
            <input type='text' name='name' value={info.name} className='new-other-order_input' onChange={(e) => infoHandler(e.target.value, "name")} />
          </div>
          <div className='new-other-order-item'>
            <h3>Mail/Redes</h3>
            <input type='text' name='redes' value={info.redes} className='new-other-order_input' onChange={(e) => infoHandler(e.target.value, "redes")} />
          </div>
          <h2 className='new-other-order_header'>Entrega:</h2>
          <div className='new-other-order-item'>
            <h3>Fecha</h3>
            <input type='date' name='description' className='new-other-order_input' onChange={(e) => infoHandler(e.target.value, "delivery_date")} />
          </div>
          <div className='new-other-order-item'>
            <h3>Observaciones</h3>
            <textarea type='text' name='description' value={info.observation} className='new-other-order_input' onChange={(e) => infoHandler(e.target.value, "observation")} />
          </div>
        </section>
        <section className='new-other-order--center'>
          <h2 className='new-other-order_header'>Orden:</h2>
          <div className='new-other-order-item'>
            <h3>Tipo de orden:</h3>
            <select type='text' name='name' className='new-other-order_input' onChange={(e) => setTypeOrder(e.target.value)}>
              <option value='en-mano'>EN MANO</option>
              <option value='devolucion'>DEVOLUCION</option>
              <option value='canje'>CANJE</option>
            </select>
          </div>
          <div className='new-other-order-item'>
            <h3>Articulos:</h3>
            <select type='text' name='selectitem' className='new-other-order_input select-input' onChange={(e) => selectItemHandler(e.target.value)}>
              <option value='en-mano'>SELECCIONA ARTICULO</option>
              {stock &&
                stock.map((stockItem) => {
                  return <option value={JSON.stringify(stockItem)}>{stockItem.name}</option>;
                })}
            </select>

            {stockFromSelected ? (
              <select type='text' name='selectstock' className='new-other-order_input select-input' onChange={(e) => selectedSizeHandler(e.target.value)}>
                <option value='en-mano'>SELECCIONA TALLE</option>
                {stockFromSelected &&
                  stockFromSelected.map((stockItem) => {
                    return <option value={JSON.stringify(stockItem)}>{stockItem.size}</option>;
                  })}
              </select>
            ) : null}

            {selectedSize ? <input type='number' value={toAddValue} name='sizecant' min={0} max={selectedSize.stock} className='new-other-order_input select-input' onChange={(e) => setToAddValue(e.target.value)} /> : null}

            {stockFromSelected && selectedSize ? <Button onClick={addArticleToSend}>AGREGAR</Button> : null}
          </div>
        </section>

        <section className='new-other-order--center'>
          <h2 className='new-other-order_header'>SELECCIONADOS:</h2>
          {articlesToSend &&
            articlesToSend.map((item, index) => {
              return (
                <div className='order-container'>
                  <h3>{item ? item.name : ""}</h3>
                  <h3>TALLE: {item ? item.size.toUpperCase() : ""}</h3>
                  <h3>CANTIDAD: {item ? item.quantity : ""}</h3>
                  <span className='delete' onClick={() => deleteArticleToSend(index)}>
                    X
                  </span>
                </div>
              );
            })}

          {articlesToSend.length > 0 ? (
            <div className='new-other-order-item check-container'>
              <h3>{typeOrder === "devolucion" ? "Sumar al stock?" : "Descontar del stock?"}</h3>
              <div className={`check ${discount ? "checked" : ""}`} onClick={(e) => setDiscount(!discount)}></div>
            </div>
          ) : null}

          {articlesToSend.length > 0 ? (
            <Button onClick={createNewOrder} style={{ marginTop: "auto" }} disabled={!isValid}>
              CREAR ORDEN
            </Button>
          ) : null}
        </section>
      </article>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    stock: state.items,
  };
};

export default connect(mapStateToProps, { createOtherOrder, setShowNewOtherOrders })(NewOtherOrder);
